.footer{
    width: 100vw;
    height: 5vh;
    background-color: var(--warm-black);
    color: var(--light-white);
    position: absolute;
    bottom: 0;
    z-index: 3;
    font-size: 0.9vw;
    text-transform: uppercase;
    line-height: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.199);
}

#project-footer{
    background-color: var(--light-white);
    color: var(--warm-black);
    box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.144);
}

#project-footer-version{
    color: inherit;
}

.footer div{
    margin: 0 2vw;
}

.footer-version{
    display: flex;
    align-items: center;
    color: var(--light-white);
}

.footer-react-icon{
    width: 1vw;
    height: auto;
    margin-right: 5px;
}