.projects-background{
    background-color: var(--light-white);
    height: 100vh;
    width: 100vw;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 2;
}

.projects-page{
    position: absolute;
    top: 58px;
    border-radius: 14px;
    left: 0;
    z-index: 2;
    width: 100vw;
    height: fit-content;
    padding-bottom: 30vh;
    /* background-color: var(--light-white); */


    /* padding-bottom: 10vw; */
    box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.219);
}

#my-projects{
    padding-bottom: 5vh;
}

.open-project{
    opacity: 0;
    animation-name: fade-up;
    animation-duration: 0.4s;
    animation-fill-mode: forwards;
    animation-delay: 0.2s;
}

.close-project{
    animation-name: fade-down;
    animation-duration: 0.4s;
    animation-fill-mode: forwards;
}

.fade-in{
    opacity: 0;
    animation-name: fade-in;
    animation-duration: 0.5s;
    animation-fill-mode: forwards;
}

.fade-out{
    animation-delay: 0.3s;
    opacity: 1;
    animation-name: fade-out;
    animation-duration: 0.5s;
    animation-fill-mode: forwards;
}

.projects-title-position{
    /* background-color: red; */
    width: 100vw;
    height: 60vh;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
}

.projects-title-container{
    /* background-color: blue; */
    width: 50%;

    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    text-align: center;
}

.projects-title{
    font-size: 9vw;
    margin-bottom: 10px;
}

.projects-subtitle{
    font-size: 0.9vw;
    line-height: 1.1;
}

#jump-to-archive{
    position: absolute;
    bottom: 7px;
    left: calc(2.5% + 10px);
}

.white-btn-1{
    display: flex;
    align-items: center;
    color: var(--warm-black);
    background-color: var(--light-white);
    padding: 2vh 2vw;
    cursor: pointer;
    border-radius: 100px;
    font-size: 0.8vw;
    transition: 0.3s;
    box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.144);
}

.white-btn-1:hover{
    transform: scale(1.05) translateY(-5px);
    box-shadow: 0px 7px 14px rgba(0, 0, 0, 0.288);
}

.white-btn-1 p{
    margin-right: 5px;
}

.all-projects{
    height: fit-content;
    /* background-color: red; */
    display: flex;
    flex-direction: column;
    align-items: center;
}

.project-line{
    /* background-color: blue; */
    width: 95%;
    height: 70vh;
    margin: 10px 0;
    display: flex;
    justify-content: center;
    /* justify-content: center; */
}

.project{
    /* background-color: green; */
    height: 100%;
    width: 100%;
    margin: 0 10px;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    color: var(--light-white);
    position: relative;
    border-radius: 15px;

    display: flex;
    flex-direction: column;
    align-items: center;

    /* border: 3px solid transparent; */

    transition: 0.3s;

    /* cursor: url("../assets/cursor2.png"), auto; */
}

.project:hover{
    transform: scale(1.01);
    box-shadow: 0 10px 25px rgba(0, 0, 0, 0.404);
    /* border: 3px solid var(--light-white); */
}

.project-location, .project-time{
    font-size: 1vw;
}

.project-quick-info{
    /* background-color: red; */
    display: flex;
    width: 90%;
    margin-top: 3vh;
    justify-content: space-between;
    text-align: right;
    /* align-items: center; */
}

.project-time{
    margin-top: 5px;
}

.project-title{
    font-size: 2.5vw;
    margin-bottom: 5px;
}

.project-description-container{
    opacity: 0;
    position: absolute;
    border-bottom-left-radius: 15px;
    border-bottom-right-radius: 15px;
    background: linear-gradient(to top, rgba(0, 0, 0, 0.932) 0%, rgba(0, 0, 0, 0) 100%);
    color: var(--light-white);
    bottom: 0;
    left: 0;
    padding: 6vh;
    transition: 0.3s;
}

.project:hover > .project-description-container{
    /* transform: translateY(-20px); */
    opacity: 1;
}

.project-description{
    transition: 0.3s;
    font-size: 1.1vw;
    text-transform: uppercase;
    line-height: 1.2;
    letter-spacing: 1px;
}

.project-description h2{
    margin-bottom: 20px;
    font-size: 2.4vw;
}

.project-description p{
    line-height: 1.2;
    letter-spacing: 1px;
    font-size: 1.1vw;
}

.project:hover .project-description{
    transform: translateY(-5px);
}

.project-set{
    display: flex;
    align-items: center;
    /* flex-direction: column; */
    /* background-color: red; */
}

.icon-container{
    /* background-color: blue; */
    display: flex;
    align-items: center;
}

.project-icon{
    margin-left: 15px;
    /* background-color: yellow; */
    width: 3vw;
    height: auto;
    color: var(--light-white);
}

#progress-icon{
    width: 1.5vw;
}

.project-links{
    margin-top: 35px;
}

.project-link{
    /* background-color: rgba(255, 255, 255, 0.397); */
    margin-right: 20px;
    padding: 12px 30px;

    color: var(--light-white);
    background-color: rgba(255, 255, 255, 0.192);
    /* border: 2px solid var(--light-white); */
    border-radius: 100px;
    font-size: 0.6vw;
    font-weight: normal;
    transition: 0.3s;
}

.project-link:hover{
    background-color: var(--light-white);
    color: black;
}

#project-1{
    background-image: url("../assets/karjat-india.jpg");
    width: 66.66%;
}

#project-2{
    background-image: url("../assets/umd-bitcamp.jpg");
    background-position: 100% 80%;
    width: 33.33%;
}

#project-3{
    background-image: url("../assets/umbc-hack.jpg");
}

#project-4{
    background-image: url("../assets/space-3.jpg");
    width: 33.33%;
}

#project-5{
    background-image: url("../assets/buzzword-8.jpg");
    width: 66.66%;
}

.projects-footer{
    width: 100vw;
    height: 40vh;
    display: flex;
    align-items: center;
    justify-content: center;
    /* background-color: orange; */
    /* margin-bottom: -25vh; */
}


/* project archive css */

.project-archive-title-container{
    width: 100vw;
    height: 40vh;
    /* background-color: blue; */
    margin-top: -40vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.archive-title{
    display: flex;
    align-items: flex-end;
    opacity: 0;
    /* background-color: orange; */
    animation-name: fade-up;
    animation-duration: 0.6s;
    animation-fill-mode: forwards;
}

.file-path-1{
    font-weight: 100;
    font-size: 1.7vw;
}

.file-path-2{
    font-weight: 1000;
    font-size: 3vw;
    margin-bottom: -2px;
}

.project-archive-container{
    width: 100vw;
    height: 80vh;
    transition: 0.3s;
    /* background-color: red; */

    opacity: 0;

    animation-name: fade-up;
    animation-duration: 0.6s;
    animation-fill-mode: forwards;
    animation-delay: 0.4s;

    display: flex;

    /* align-items: center; */
    justify-content: center;
}

.project-archive-line{
    /* background-color: yellow; */
    display: flex;
    width: 80%;
    height: fit-content;
    justify-content: space-evenly;
}

.archive-project{
    background-color: var(--light-white);
    width: 23%;
    padding: 2vw;
    color: var(--warm-black);
    border-radius: 20px;
    position: relative;

    box-shadow: 0 3px 25px rgba(0, 0, 0, 0.137);

    /* cursor: url('../assets/mac-click.png'),auto; */

    display: flex;
    flex-direction: column;
    justify-content: space-between;

    transition: 0.3s;
}

.archive-project:hover{
    transform: scale(1.03) translateY(-20px);
    box-shadow: 0 6px 27px rgba(0, 0, 0, 0.212);
}

.archive-project:hover .archive-project-folder-icon{
    transform: scale(1.2);
    opacity: 1;
}

.archive-project-folder-icon{
    position: absolute;
    right: 0;
    top: 0;
    padding: 2vw;
    width: 2vw;
    height: auto;
    opacity: 20%;

    transition: 0.3s;
}

.archive-project h2{
    text-transform: uppercase;
    font-weight: 1000;
    font-size: 2.4vw;
    letter-spacing: 1px;
    margin-bottom: 10px;
}

.archive-project h3{
    text-transform: uppercase;
    font-size: 1.2vw;
    margin-bottom: 13px;
}

.archive-tag-list{
    /* background-color: red; */
    display: flex;
    max-width: 90%;
    flex-wrap: wrap;
}

.archive-tag{
    text-transform: uppercase;
    background-color: var(--warm-black);
    color: var(--light-white);
    padding: 4px 8px;
    font-size: 0.7vw;
    margin-right: 5px;
    margin-bottom: 5px;
    border-radius: 100px;
}

.archive-project-description{
    /* margin-top: 18vh; */
    font-size: 0.8vw;
    line-height: 1.4;
    letter-spacing: 0.1px;
    text-transform: uppercase;
    
    /* background-color: red; */
}

.archive-card-top{
    /* background-color: pink; */
    margin-bottom: 13vh;
}

.archive-card-bottom{
    /* background-color: green; */
}

.fade-away-up{
    opacity: 1;

    animation-name: fade-away-up;
    animation-duration: 0.8s;
    animation-fill-mode: forwards;
}

.footer-move-down{
    animation-name: fade-down;
    animation-duration: 1s;
}

@keyframes fade-away-up {
    0% {
        opacity: 1;
        transform: translateY(0);
    }
    100% {
        opacity: 0;
        transform: translateY(-20px);
    }
}

@keyframes fade-up {
    0% {
        opacity: 0;
        transform: translateY(13vh);
    }
    100% {
        opacity: 100%;
        transform: translateY(0);
    }
}

@keyframes fade-in {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes fade-down {
    0% {
        opacity: 100%;
        transform: translateY(0);
    }
    100% {
        opacity: 0;
        transform: translateY(13vh);
    }
}

@keyframes fade-out {
    0% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}