.landing-page{
    /* background-image: url('../assets/myphotos/mp-greenhouse.JPG'); */
    /* background-position: center; */
    background-size: cover;
    background-repeat: no-repeat;
    width: 100vw;
    height: 100vh;
    color: var(--light-white);
    z-index: 0;
    animation-name: move-bg;
    animation-duration: 200s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
    animation-direction: alternate;

}

/* #landing-background-0{
    background-image: url('../assets/myphotos/mp-greenhouse.JPG');
} */

#landing-background-1{
    background-image: url('../assets/landing-bgs/bg-niagra.JPG');
}

@keyframes move-bg {
    0% {
        background-position: center;
    }
    100% {
        background-position: top;
    }
}

.title-container{
    /* background-color: red; */
    width: 100%;
    height: 88.7vh;
    display: flex;
    /* justify-content: center; */
    align-items: center;
    padding-left: 6vw;

    position: absolute;
    bottom: 0;
    left: 0;
}

.my-name{
    font-size: 12vw;
    letter-spacing: 5px;
    line-height: 0.9;
    margin-bottom: 20px;
    
    opacity: 0;
    animation-name: fade-up;
    animation-fill-mode: forwards;
    animation-duration: 0.5s;
    animation-delay: 0.3s;

}

.sub-title{
    font-size: 1.2vw;
    line-height: 1.1;
    margin-left: 5px;

    opacity: 0;
    animation-name: fade-up;
    animation-fill-mode: forwards;
    animation-duration: 0.5s;
    animation-delay: 0.6s;
}

.page-nav-container{
    position: fixed;
    right: 3vw;
    top: 58px;
    height: calc(100vh - 58px);
    width: fit-content;
    /* background-color: blue; */
    pointer-events: none;
    z-index: 1;
    display: flex;
    align-items: flex-end;
}

.page-nav{
    pointer-events: all;
    width: 100%;
    height: fit-content;
    /* background-color: red; */
    display: flex;
    flex-direction: column;
    align-items: center;
}

.page-link{
    /* background-color: orange; */
    /* border: 1.5px solid var(--light-white); */
    cursor: pointer;
    margin: 3vh 0;
    transition: 0.3s;
    display: flex;
    align-items: center;
}

.page-line{
    background-color: var(--light-white);
    width: 1.5px;
    height: 16vh;
    margin-top: 2vh;
    opacity: 0;

    animation-name: page-links-stem;
    animation-delay: 0.9s;
    animation-duration: 0.3s;
    animation-fill-mode: forwards;
}

.page-link-icon{
    width: 1.6vw;
    height: auto;
    color: var(--light-white);
    opacity: 0;

    animation-name: fade-in-right;
    animation-duration: 0.4s;
    animation-fill-mode: forwards;
}

#md-folder{
    animation-delay: 1.4s;
}

#md-camera{
    animation-delay: 1.2s;
}

#md-work{
    animation-delay: 1s;
}

.page-link::before{
    position: absolute;
    display: inline-block;
    right: 2vw;
    font-size: 1vw;
    opacity: 0;
    transition: 0.3s;
    pointer-events: none;
    color: var(--light-white);
}

#project-icon::before{
    content: "PROJECTS";
}

#photo-icon::before{
    content: "PHOTOS";
}

#work-icon::before{
    content: "EXPERIENCE";
}

.page-link:hover::before{
    opacity: 1;
    transform: translateX(-20px);
}

.page-link:hover{
    /* transform: scale(1.1); */
}

.spawn-in{
    opacity: 1;
}

@keyframes fade-in-left {
    0% {
        opacity: 0;
        transform: translateX(-20%);
    }
    100% {
        opacity: 1;
        transform: translateX(0);
    }
}

@keyframes page-links-stem{
    0% {
        opacity: 0;
        transform: translateY(100%);
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes fade-in-right{
    0% {
        opacity: 0;
        transform: translateX(50%);
    }
    100% {
        opacity: 1;
        transform: translateX(0);
    }
}